import React,{Fragment} from 'react';
import IconsListItem from './iconsListItem';
import styled from 'styled-components';
import { BREAKPOINT_SM } from '../types';

const CategoryHeader = styled.h3`
    margin-left: 15px;
    font-weight: 400;
    font-size: 16px;
    color: #b8b8b8;
    @media screen and (max-width: ${BREAKPOINT_SM}) {
        font-size: 14px;
    }
`;
const IconsList = (props) => {
var category='';
    const iconsItems = props.icons.map((icon,num) => {
        if(icon.category.name!==category){
            category=icon.category.name;
            return [
              
                <CategoryHeader key={icon.category.name}>{icon.category.name}</CategoryHeader>,
                <IconsListItem
            key={icon.id}
            icon={icon}
            num = {num+1}
            dark={props.dark}
            style = {props.style}
            />
                
                
        ];
        }
       
        return (<IconsListItem
            key={icon.id}
            icon={icon}
            num = {num+1}
            dark={props.dark}
            style = {props.style}
            />
        );
    });

    return (

        <div className="icon-list animated delay50 fadeIn">
         <div className="container">
            {iconsItems}
            </div>
        </div>
    );

};


export default IconsList;